import { useContext } from 'preact/hooks';
import { BuildContext } from '../../lib/context';
import tw, { styled } from 'twin.macro';

const NameStyles = tw.div`flex`;

const Name = () => {

    const { dataApiBuildData: { data: { lol: { champion: { build: { championSlug = '' } = {} } = {} } = {} } = {} } = {}, squidexApiBuildData: { data: squidexData = {} }, loading = true } = useContext(BuildContext);
    const { championCommonInfo = [] } = squidexData;
    const { flatData: { name = '', title = '' } = {} } = championCommonInfo[0] || {};

    const NameTextStyles = styled.span(() => [
        tw`text-white font-normal text-xs uppercase`,
        loading && tw`bg-white animate-pulse`,
    ]);

    const nameTitle = `${name} build`;

    return (
        <NameStyles>
            <NameTextStyles>{nameTitle}</NameTextStyles>
        </NameStyles>
    );
};

export default Name;